.register__container {
    background-color: #000000;
    color: #FFFFFF;
    font-family: 'Inter', 'Arial', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-weight: 400;
    min-width: 320px;
    max-width: 1280px;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 auto;
    padding: 40px 0 60px 0;
  
  }
  
  @media screen and (max-width:568px) {
    .register__container {
      padding: 28px 0 36px;
    }
  }
  